import React from 'react';
import classes from'./modal.module.css';
import { HashRouter as Router,withRouter} from 'react-router-dom'


class Modal extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
        }
    }
    
    // È¶ñÊ¨°Ê∏≤Êüì‰ΩøÁî®Áà∂ÁªÑ‰ª∂ÁöÑÁä∂ÊÄÅÊõ¥Êñ∞ modal ‰∏≠ÁöÑ visible Áä∂ÊÄÅÔºåÂè™Ë∞ÉÁî®‰∏ÄÊ¨°
    componentDidMount() {
        this.setState({
            visible: this.props.visible
        })
    }

    // ÊØèÊ¨°Êé•Êî∂ props Â∞±Ê†πÊçÆÁà∂ÁªÑ‰ª∂ÁöÑÁä∂ÊÄÅÊõ¥Êñ∞ modal ‰∏≠ÁöÑ visible Áä∂ÊÄÅÔºåÈ¶ñÊ¨°Ê∏≤Êüì‰∏ç‰ºöË∞ÉÁî®
    componentWillReceiveProps(props) {
        this.setState({
            visible: props.visible
        })
    }

    handleClose = () => {
        // const { onClose } = this.props
        // onClose && onClose()
        this.setState({
            visible: false
        })
        this.props.change();
    }
    handleConfirm = () => {
        // const { onConfirm } = this.props
        // onConfirm && onConfirm()
        this.setState({
            visible: false
        })
        this.props.change();
          }
    
    handleMask = () => {
        this.setState({
            visible: false
        })
    }
    render() {
        // ÈÄöËøáÁà∂ÁªÑ‰ª∂‰º†ÈÄíÁöÑ visible ÊéßÂà∂ÊòæÈöê
        const { title, children } = this.props
        const { visible = true } = this.state
        return visible &&
        (
            <Router >
            <div className={classes.wrapper}>
                <div className={classes.modal}>
                    <div className={classes.title}>{title}</div>
                    <div className={classes.content}>{children}</div>
                    <div className={classes.operator}>
                        <button className={classes.close} onClick={this.handleClose}>ÂèñÊ∂à</button>
                        <button className={classes.confirm} onClick={()=>{
                            this.props.handleConfirm();
                            this.props.history.push('/burger/info'   
                            )
                            
                        }} > Á°ÆÂÆö
                        </button>
         
                    </div> 
                </div>
               
            </div>  
               
            </Router>
        )
    }
}

export default withRouter(Modal)


































































