import React from 'react';
import classes from './BuildControls.module.css';
import BuildControl from '../BuildControls/BuildControl';
import {connect} from 'react-redux';
import * as actions from '../../../store/actions/index'
      //Array.map() ÊñπÊ≥ïËøîÂõû‰∏Ä‰∏™Êñ∞Êï∞ÁªÑÔºå
      //Êï∞ÁªÑ‰∏≠ÁöÑÂÖÉÁ¥†‰∏∫ÂéüÂßãÊï∞ÁªÑÂÖÉÁ¥†Ë∞ÉÁî®ÂáΩÊï∞Â§ÑÁêÜÂêéÁöÑÂÄºÔºåÂêåÊó∂‰∏ç‰ºöÊîπÂèòÂéüÊù•ÁöÑÊï∞ÁªÑ„ÄÇ
         //bind()ÊñπÊ≥ï‰∏ªË¶ÅÂ∞±ÊòØÂ∞ÜÂáΩÊï∞ÁªëÂÆöÂà∞Êüê‰∏™ÂØπË±°Ôºåbind()‰ºöÂàõÂª∫‰∏Ä‰∏™ÂáΩÊï∞Ôºå
         //ÂáΩÊï∞‰ΩìÂÜÖÁöÑthisÂØπË±°ÁöÑÂÄº‰ºöË¢´ÁªëÂÆöÂà∞‰º†ÂÖ•bind()Á¨¨‰∏Ä‰∏™ÂèÇÊï∞ÁöÑÂÄºÔºå
         //‰æãÂ¶ÇÔºåf.bind(obj)ÔºåÂÆûÈôÖ‰∏äÂèØ‰ª•ÁêÜËß£‰∏∫obj.f()ÔºåËøôÊó∂ÔºåfÂáΩÊï∞‰ΩìÂÜÖÁöÑthisËá™ÁÑ∂ÊåáÂêëÁöÑÊòØobj
        //IngredientAddedÊòØbuildControlsÂÆö‰πâÁöÑ
const controls = [
  {label : 'ü•óSalad  ', type: 'salad'},
  {label : 'ü•ìBacon  ', type: 'bacon'},
  {label : 'üßÄCheese ', type: 'cheese'},
  {label : 'ü•©Meat   ', type: 'meat'},
]
const buildControls = (props) => (
  <div className={classes.BuildControls}>
     <div className={classes.lessmore}>
          <h1>Current Price: <strong>üí∞{props.totalPrice.toFixed(2)}</strong> </h1>
          {controls.map(control => {
              return <BuildControl
              key={control.label} 
              label={control.label} 
              add={() => props.IngredientAdded(control.type)}
              remove={() => props.RemoveIngredient(control.type)}
              disabled={props.disabled[control.type]}
              />
          })}
          <br/>
           <p>
        <button onClick={props.click} className={classes.OrderButton1}
         disabled={!props.prsable}>ORDER NOW</button> 
        </p> 
      </div>
     <div className={classes.auto}  >
        <h2> <strong>Fear of choice? Try ordering with one click üëá</strong></h2>
        <p className={classes.Label}> Small ü•ó1 ü•©1 üßÄ1 ü•ì1   &nbsp;
        <a onClick={props.small}className={classes.OrderButton}> Small</a></p><br/>

        <p className={classes.Label}> Double meat ü•ó1 ü•©2 üßÄ1 ü•ì1 &nbsp;
        <a onClick={props.meat}className={classes.OrderButton}> Double Meat</a></p><br/>

        <p className={classes.Label}> BigDouble ü•ó2 ü•©2 üßÄ2 ü•ì2 &nbsp;
        <a onClick={props.big}className={classes.OrderButton}>Burger king</a></p><br/>
        <p><a  href="javascript:location.reload();" className={classes.OrderButton}> Restart</a></p>
       
  </div> 
  </div>
  
)

const mapStateToProps = state => {
  return {
    igrds: state.burgerBuilder.ingredients,
    price: state.burgerBuilder.totalPrice,
    sum: state.burgerBuilder.sumprice
  };
}

const mapDispatchToProps = dispatch =>{
  return {
    small:()=>dispatch(actions.small()),
    meat:()=>dispatch(actions.meat()),
    big:()=>dispatch(actions.big()),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(buildControls);